@media all and (min-width: 1024px) {
  .input-forms {
    padding: 70px;
    padding-bottom: 15px;
    padding-top: 15px;

    input {
      width: 350px;
    }
  }
}

@media all and (max-width: 1024px) {
  .input-forms {
    padding: 60px;

    input {
      width: 330px;
    }
  }
}

@media all and (max-width: 768px) {
  .input-forms {
    padding: 50px;

    input {
      width: 300px;
    }
  }
}


.MasterBoard {
  width: 100%;
  height: auto;
  font-size: 1rem;
  display: flex;
  min-width: 1024px;

  .Masterlist {
    width: 50%;
  }

  .Masterwrite {
    width: 50%;
  }

  .userList_button {
    bottom: 0;

    button {
      margin: 10px;
      border-radius: 5px;
    }
  }
}

.MasterBoard2 {
  margin: auto;
  display: flex;
  flex-direction: column;
  font-family: 'Noto Sans KR', sans-serif;

  .Masterlist-title {
    text-align: center;
    margin: 15px 0 15px 0;
    font-size: 1.5rem;
    line-height: 45px;
    height: 3rem;
    max-width: 102.3rem;
    min-width: 1024px;
    width: 100%;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    border: 1px white solid;
    font-weight: bold;
  }

  .Masterlist-contents {

    display: flex;
    min-width: 1024px;
    width: 100%;
    height: 51rem;
    margin: 11px 0;
    max-width: 102.3rem;
    min-width: 1024px;
    .Masterlist {
      width: 50%;
    }

    .Masterwrite {
      width: 50%;
    }

    .userList_button {
      bottom: 0;

      button {
        margin: 10px;
        border-radius: 5px;
      }
    }
  }
}
.MasterList_wrapper {
  background-color: white;
  text-align: center;
  width: 98%;
  margin-left: 2%;
  height: 40rem;
  border: 1px solid black;
  border-radius: 5px;

  .MasterList_table {
    overflow:auto;
    width: 96%;
    margin: 2%;
    border: 1px solid black;
    border-radius: 5px;

    .MasterList_thead {
      background-color: rgba(196, 196, 196, 0.5);

      .MasterList_tr {
        border: 1px solid black;
      }
    }
  }

}


